.container {
    max-width: 1320px;
    margin: 0 auto;
    padding: 0px 12px;
    @include pad{
        max-width: 992px;
    }
    @include mobile{
        max-width: 768px;
    }
}

/*df*/
.d-flex{
    display: flex;
}

.justify-content-center{
    justify-content: center;
}

.justify-content-spacebetween{
    justify-content: space-between;
}

.align-items-center{
    align-items: center;
}

.gap-1{
    gap: 64px;
}

.gap-2{
    gap: 48px;
}

.gap-3{
    gap: 32px;
}

/*fz*/

.font-size-1{
    font-size: 14px;
}

.font-size-2{
    font-size: 16px;
}

.font-size-3{
    font-size: 20px;
}

.font-size-4{
    font-size: 36px;
}

/*fw*/
.font-regular{
    font-weight: 500;
}

.font-medium{
    font-weight: 700;
}

.font-blod{
    font-weight: 900;
}

/*color*/
.bg-primary{
    background-color: $primary;
}

.text-light{
    color: $white;
}

/*distance*/

.mt-1{
    margin-top: 24px;   
}